body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.baseContainer {
    padding: 0px;
    margin: 0px; 
}

.timeline::after {
    content: '';
    width: 3px;
    position: fixed;
    background-color: #d32e2f;
    top: 0;
    bottom: 0;
    /* left: 10%; */
    margin-left: 20px;
}

.blockContainer{
    display: flex;
    padding: 0px;
    margin-top: 18px;
    margin-left: 5px;
    z-index: 3;
}

.timestamp {
    background: navy;
    /* text-align: center; */
    height: 25px;
    width: 55px; 
    z-index: 1;
    text-align: center;
}

.timestamp-text {
    color: white;
    margin: 0px;
    padding: 4px 0 5px 0;
 
}

.paper {
    /* margin-left: 25px; */
    padding-top: 4px;
    padding-left: 7px;
    padding-right: 7px;
    height: calc(55px + (65 - 55) * ((100vw - 300px) / (1600 - 300)));
    /* 15vw; */
    width: calc(220px + (350 - 220) * ((100vw - 300px) / (1600 - 300)));
}

.timeline-text {
    padding: 0px;
    padding-top: 0px;
    padding-left: 5px;
    margin: 0px;
    color: black;
    font-size: calc(11pt + (13 - 11) * ((100vw - 300px) / (1600 - 300)));
}

